<script lang="ts">
    import { dealsEffects, dealsStore } from "../deals.store";
    import { approvalsStore } from "../../approval.store";
    import { clockStore } from "../../clock.store";
    import { computeDealForeignCurrencyDerrivations } from "./DealForeignCurrency";
    import DealBareCmp from "./../Deal/Deal.BareCmp.svelte";
    import SelectDropdown from "../../../generic-components/SelectDropdown.svelte";

    export let dealId: number;

    $: dealState = computeDealForeignCurrencyDerrivations(
        $dealsStore,
        $approvalsStore,
        $clockStore,
        dealId
    );
</script>

<div class="car-purchase-deal car-purchase-deal-with-foreign-currency">
    <DealBareCmp {dealId} />

    <div class="car-purchase-downpayment-currency-label">
        Please select currency
    </div>

    <SelectDropdown
        selectAttributes={{ class: "car-purchase-downpayment-currency" }}
        hasEmptyOption={false}
        availableItems={dealState.deal.currenciesAvailable}
        modelState={dealState.deal.businessParams.downpaymentCurrency}
        disabled={dealState.isLoadingAny ||
            dealState.deal.businessParams.isDealFinalized}
        onSelect={(currency) =>
            dealsEffects.setCurrncyAndReloadExchangeRate(dealsStore, [
                dealId,
                currency,
            ])}
    />
</div>

